.main-content {
  max-width: 900px;
  margin: 3rem auto;
  padding: 2rem;
  background-color: #fafafa;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  font-family: 'Inter', sans-serif;
}

h1 {
  font-weight: 700;
  font-size: 28px;
  margin-bottom: 20px;
}

.filter-container {
  display: flex;
  gap: 15px;
  margin-bottom: 20px;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.filter-input,
.filter-select {
  padding: 8px 12px;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: white;
  font-family: 'Inter', sans-serif;
  outline: none;
  transition: border-color 0.3s ease;
  min-width: 150px;
}

.filter-input:focus,
.filter-select:focus {
  border-color: #1abc9c;
}

.asset-table {
  width: 100%;
  border-collapse: collapse;
  font-family: 'Inter', sans-serif;
}

.asset-table th,
.asset-table td {
  border: 1px solid #ddd;
  padding: 0.75rem 1rem;
  text-align: center;
}

.asset-table th {
  background-color: #2c3e50;
  color: white;
}

.asset-table tbody tr:hover {
  background-color: #f5f5f5;
}

.status-instock {
  color: #27ae60;
  font-weight: 600;
}

.status-assigned {
  color: #c0392b;
  font-weight: 600;
}

.action-buttons button {
  margin: 0 0.25rem;
  padding: 0.3rem 0.7rem;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  color: white;
}

.edit-btn {
  background-color: #2980b9;
}

.edit-btn:hover {
  background-color: #1c5980;
}

.delete-btn {
  background-color: #c0392b;
}

.delete-btn:hover {
  background-color: #82241f;
}

