/* Purchases.css */

.p-4 {
  padding: 1rem 1.5rem;
  max-width: 700px;
  margin: 2rem auto;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
}

h2 {
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  color: #2c3e50;
  text-align: center;
}

form {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 1.5rem;
}

input {
  padding: 10px 12px;
  font-size: 1rem;
  border: 1.5px solid #ccc;
  border-radius: 6px;
  transition: border-color 0.2s ease;
}

input:focus {
  outline: none;
  border-color: #0077cc;
  box-shadow: 0 0 5px rgba(0, 119, 204, 0.5);
}

button {
  padding: 10px 15px;
  background-color: #0077cc;
  color: white;
  font-weight: 600;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #005fa3;
}

ul {
  list-style: none;
  padding-left: 0;
  max-height: 400px;
  overflow-y: auto;
  border-top: 1.5px solid #ddd;
}

ul li {
  padding: 0.7rem 1rem;
  border-bottom: 1px solid #eee;
  font-size: 1rem;
  line-height: 1.4;
  color: #444;
}

ul li:nth-child(odd) {
  background-color: #f9f9f9;
}
