/* Users.css */

.users-container {
  max-width: 600px;
  margin: 2.5rem auto;
  padding: 1.5rem 2rem;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.1);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #2d2d2d;
}

.users-container h1 {
  margin-bottom: 1.5rem;
  font-weight: 700;
  font-size: 2rem;
  text-align: center;
  color: #1e2a38;
}

.users-container p {
  font-size: 1rem;
  text-align: center;
  color: #555;
}

.users-list {
  list-style-type: none;
  padding-left: 0;
  border-top: 1px solid #eee;
}

.users-list li {
  padding: 0.75rem 0;
  border-bottom: 1px solid #eee;
  font-size: 1.1rem;
  color: #444;
}

.users-list li:last-child {
  border-bottom: none;
}
